.App {
  text-align: center; 
  font-family: 'Balsamiq Sans', cursive;
}

.App__button {
  padding: 0.5rem;
  margin: 0.5rem;
  cursor: pointer;
  outline: 0;
  border-radius: 0.5rem;
}

.App__button--small {
  margin: 0.5rem;
  cursor: pointer;
  outline: 0;
}

.App__button--blue {
  background-color: #DEF3FD;
  border-color: #B9E9FF;
}

.App__button--focusBlue {
  background-color: #acc9ff;
  border-color: #8bb3fd;
  /* outline: 2px solid #7fd7ff; */
  outline: 0;
  box-shadow: 0 0 3pt 2pt #7fd7ff;
  /* border: 2px solid #8bb3fd */
  
}

