  background-color: #acc9ff;
  border-color: #8bb3fd;
  outline: 2px solid #7fd7ff
} */

.App__button--red {
  background-color: #FDDFDF;
  border-color: #FFA1A1;
}

.App__button--focusRed {
  background-color: #FFA1A1;
  border-color: #FFA1A1;
  /* outline: 2px solid #ff7e7e; */
  outline: 0;
  box-shadow: 0 0 3pt 2pt #ff7e7e;;
}

